.word-item{
  border: 1px solid rgb(131, 131, 131);
  background-color: #ffff4d;
  min-width: 100px;
  height: 60px;
  
  font-size: 20px;
  border-radius: 10px;
  text-align: center;
  flex-wrap: wrap;
  padding: 5px;
  margin: 5px;
}
.word-item:hover{
  box-shadow: 2px -1px 5px #8f8f8f;
}
